@import url(https://fonts.googleapis.com/css?family=Barlow:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i|Red+Hat+Display:400,400i,500,500i,700,700i,900,900i|Varela+Round&display=swap);
body {
  background: #3a3a3a; }

* {
  margin: 0;
  padding: 0; }

.note {
  border: solid 2px #bdbdbd;
  border-radius: 2rem; }
  .note .favorite {
    color: #b4b4b4;
    font-size: 2rem;
    color: red; }
    .note .favorite:hover {
      cursor: pointer;
      font-size: 2.5rem;
      color: #00a2ff; }
  .note .noFavorite {
    color: #b4b4b4;
    font-size: 2rem; }
    .note .noFavorite:hover {
      cursor: pointer;
      font-size: 2.5rem;
      color: #00a2ff; }
  .note .remove {
    color: #b4b4b4;
    font-size: 2rem; }
    .note .remove:hover {
      cursor: pointer;
      font-size: 2.5rem;
      color: #00a2ff; }
  .note .edit {
    color: #b4b4b4;
    font-size: 2rem;
    color: white; }
    .note .edit:hover {
      cursor: pointer;
      font-size: 2.5rem;
      color: #00a2ff; }
  .note .noEdit {
    color: #b4b4b4;
    font-size: 2rem; }
    .note .noEdit:hover {
      cursor: pointer;
      font-size: 2.5rem;
      color: #00a2ff; }
  .note .detail {
    color: #b4b4b4;
    font-size: 2rem; }
    .note .detail:hover {
      cursor: pointer;
      font-size: 2.5rem;
      color: #00a2ff; }
  .note .noteText1,
  .note .noteText2 {
    font-family: 'Varela Round', sans-serif;
    font-weight: 200;
    font-size: 2rem;
    color: rgba(228, 228, 228, 0.911);
    font-size: 1.2rem;
    text-transform: capitalize;
    color: #949494; }
  .note .noteText2 {
    color: #8d8d8d; }
  .note .noteContentEdit,
  .note .locationEdit {
    background-color: rgba(255, 255, 255, 0);
    border: none;
    border-bottom: solid 2px;
    border-color: aliceblue;
    font-family: 'Varela Round', sans-serif;
    font-weight: 200;
    font-size: 2rem;
    color: rgba(228, 228, 228, 0.911);
    font-size: 1.5rem; }
    @media (max-width: 575.98px) {
      .note .noteContentEdit,
      .note .locationEdit {
        font-size: 1rem; } }

.notesContainer .subtitle {
  font-family: 'Barlow', sans-serif;
  font-weight: 400;
  font-size: 3rem;
  color: rgba(228, 228, 228, 0.911);
  font-size: 1rem;
  color: #a1ffef;
  text-decoration: none; }

.notesContainer .goFavorites {
  color: #b4b4b4;
  font-size: 2rem;
  font-size: 1.5rem;
  text-decoration: none; }
  .notesContainer .goFavorites:hover {
    cursor: pointer;
    font-size: 2.5rem;
    color: #00a2ff; }

.notesContainer .notesHeader,
.notesContainer .notesBody,
.notesContainer .notesFooter {
  background-color: rgba(0, 0, 0, 0.5); }

.notesContainer .notesHeader {
  font-family: "Lobster", cursive;
  text-align: center;
  color: white; }

.notesContainer .notesBody {
  margin-top: 20px;
  padding: 2rem;
  color: white; }

.notesContainer .notesFooter {
  bottom: 0;
  position: fixed;
  left: 0;
  width: 100%; }

.favorites .title {
  font-family: 'Varela Round', sans-serif;
  font-weight: 700;
  font-size: 4rem;
  color: rgba(228, 228, 228, 0.911); }

.favorites .noteContent {
  font-family: 'Varela Round', sans-serif;
  font-weight: 200;
  font-size: 2rem;
  color: rgba(228, 228, 228, 0.911);
  font-size: 1.5rem;
  text-transform: capitalize; }

.favorites .home {
  background: #707070;
  border: solid 2px #ccfff7;
  font-weight: 700;
  color: white;
  border-radius: 2rem;
  -webkit-border-radius: 2rem;
  -moz-border-radius: 2rem;
  -ms-border-radius: 2rem;
  -o-border-radius: 2rem; }

.favorites button {
  background: none;
  color: white;
  border: none; }

.form .noteContent,
.form .location {
  background-color: rgba(255, 255, 255, 0);
  border: none;
  border-bottom: solid 2px;
  border-color: aliceblue;
  font-family: 'Varela Round', sans-serif;
  font-weight: 200;
  font-size: 2rem;
  color: rgba(228, 228, 228, 0.911); }

.form .done {
  background-color: #868686;
  color: white;
  border: none; }

.form .chulo {
  color: #b4b4b4;
  font-size: 2rem;
  font-size: 1.5rem; }
  .form .chulo:hover {
    cursor: pointer;
    font-size: 2.5rem;
    color: #00a2ff; }

